import { HColor, MyUI } from "@macashipo/mlib"
import { Component } from "react"
import { ApiGeneric } from "src/services/apiServices"
import styled from "styled-components"
import DefaultAvatar from "src/assets/images/avatar.jpg"
import { Nav, NavItem, NavLink, TabContent, TabPane } from "reactstrap"

const dataTemp = [
  {
    ProjectId: "1",
    ProjectName: "Alliance",
    Name: "Alliance",
    Status: "Progress",
    CardColor: "rgb(46, 43, 239)",
    Percent: 40,
    Description: "",
    Note: "",
    AssignTo: [
      {
        Id: 1,
        Name: "Tran",
        Avatar: `data:image/jpeg;base64,`,
      },
      {
        Id: 2,
        Name: "Van",
        Avatar: `https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTPQzg2-modiBeSBIckt_NcpipPPGQfZA_dbQ&usqp=CAU`,
      },
      {
        Id: 3,
        Name: "Thanh",
        Avatar: `data:image/jpeg;base64,`,
      },
      {
        Id: 3,
        Name: "Ha",
        Avatar: `data:image/jpeg;base64,`,
      },
    ],
    ReportTo: "",
  },
  {
    ProjectId: "2",
    ProjectName: "Reduce",
    Name: "Reduce",
    Status: "Finished",
    CardColor: "rgb(102, 180, 51)",
    Percent: 100,
    Description: "",
    Note: "",
    AssignTo: [
      {
        Id: 1,
        Name: "Tran",
        Avatar: `data:image/jpeg;base64,`,
      },
      {
        Id: 2,
        Name: "Van",
        Avatar: `https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTPQzg2-modiBeSBIckt_NcpipPPGQfZA_dbQ&usqp=CAU`,
      },
    ],
    ReportTo: "",
  },
]
const ColorDefault = "rgb(46, 43, 239)"

class MP_PRJ_Info extends Component {
  constructor(props) {
    super(props)
    this.state = {
      Data: [],
      open: true,
      currentActiveTab: "1",
    }
  }
  componentDidMount() {}

  requestOptions = () => {
    ApiGeneric({
      request: {
        method: "POST",
        path: "/api/v1/ApiDocument_Demo",
        name: "List",
      },
      data: {},
      successCallBack: response => {
        if (response.Data.Data) {
          this.setState({ Data: response.Data.Data })
        }
      },
      errorCallBack: err => {
        console.log(err)
      },
    })
  }

  toggle = tab => {
    if (this.state.currentActiveTab !== tab)
      this.setState({ currentActiveTab: tab })
  }

  render() {
    return (
      <WrapLayout>
        <div className="tab">
          <Nav tabs>
            <NavItem
              className={`${
                this.state.currentActiveTab === "1" ? "nav-item-active" : ""
              }`}
            >
              <NavLink
                onClick={() => {
                  this.toggle("1")
                }}
              >
                Card
              </NavLink>
            </NavItem>
            <NavItem
              className={`${
                this.state.currentActiveTab === "2" ? "nav-item-active" : ""
              }`}
            >
              <NavLink
                onClick={() => {
                  this.toggle("2")
                }}
              >
                Table
              </NavLink>
            </NavItem>
          </Nav>
          <TabContent activeTab={this.state.currentActiveTab}>
            <TabPane tabId="1">
              <div className="wrap-manager-card row">
                {dataTemp?.map((item, i) => {
                  return (
                    <div className="col-4" key={i}>
                      <MyUI type="card_task" data={item} />
                    </div>
                  )
                })}
              </div>
            </TabPane>
            <TabPane tabId="2">
              <div className="wrap-table">
                <table class="table">
                  <colgroup>
                    <col width={"5%"} />
                    <col width={"20%"} />
                    <col width={"20%"} />
                    <col width={"20%"} />
                    <col width={"20%"} />
                  </colgroup>
                  <thead>
                    <tr>
                      <th scope="col">#</th>
                      <th scope="col">Task Name</th>
                      <th scope="col">Status</th>
                      <th scope="col">Assigner</th>
                      <th scope="col">Description</th>
                    </tr>
                  </thead>
                  <tbody>
                    {dataTemp?.map((item, i) => {
                      const cardColor = item.CardColor || ColorDefault
                      return (
                        <tr>
                          <td>1</td>
                          <td>
                            <div className="project-name">
                              {item.ProjectName}
                            </div>
                            <div className="task-name">{item.Name}</div>
                          </td>
                          <td>
                            <div
                              className="wrap-status"
                              style={{ background: item.CardColor }}
                            >
                              {item.Status}
                            </div>
                          </td>
                          <td>
                            <div className="wrap-assigner">
                              {item.AssignTo?.length > 2 ? (
                                <>
                                  {item.AssignTo?.slice(0, 2)?.map(
                                    (assigner, i) => {
                                      return (
                                        <div className="assigner-item" key={i}>
                                          <img
                                            className="assigner-img"
                                            src={
                                              assigner.Avatar || DefaultAvatar
                                            }
                                            alt="assigner"
                                          />
                                        </div>
                                      )
                                    }
                                  )}
                                  <div className="assigner-item assigner-more">
                                    +{item.AssignTo.length - 2}
                                  </div>
                                </>
                              ) : (
                                item.AssignTo?.map((assigner, i) => {
                                  return (
                                    <div className="assigner-item" key={i}>
                                      <img
                                        className="assigner-img"
                                        src={assigner.Avatar || DefaultAvatar}
                                        alt="assigner"
                                      />
                                    </div>
                                  )
                                })
                              )}
                            </div>
                          </td>
                          <td>
                            <div>2 Week left</div>
                          </td>
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
              </div>
            </TabPane>
          </TabContent>
        </div>
      </WrapLayout>
    )
  }
}

const WrapLayout = styled.div`
  height: 100%;
  overflow-y: auto;
  padding: 1rem;
  & .nav-item {
    & .nav-link {
      background: #fff;
      border-color: #e9ecef #e9ecef #dee2e6;
      border-width: 1px;
    }
    &.nav-item-active {
      & .nav-link {
        background: #033c73;
        color: #fff;
        &:hover {
          color: #fff !important;
        }
      }
    }
  }

  & .row {
    margin: 0;
    & .col-4 {
      padding: 2rem 1rem 1rem;
    }
  }
  & .wrap-table {
    & .table {
      & tr {
        border-left: 1px solid #e9ecef;
        border-right: 1px solid #e9ecef;
        background: #fff;
      }
      & td {
        vertical-align: middle;
      }
      & .project-name {
        color: #887c7c;
      }
      & .task-name {
        color: #000;
      }
      & .wrap-status {
        width: 150px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        border-radius: 15px;
        color: #fff;
      }
      & .wrap-assigner {
        display: flex;
        margin-left: 10px;
        min-height: 40;
        & .assigner-item {
          margin-left: -10px;
          width: 40px;
          height: 40px;
          border-radius: 50%;
          overflow: hidden;
          border: 1px solid #d9d9d9;
          &.assigner-more {
            background: #d9d9d9;
            color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 20px;
          }
        }
        & .assigner-img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
`

export default MP_PRJ_Info
